<style lang="scss" scoped src="./main/main.scss"></style>
<template>
    <div class="searchdata">
        <v-loading v-show="isLoading"></v-loading>
        <div class="search">
            <input type="text" name="key" id="key" v-model="key" placeholder="搜索便签">
            <button @click="searchMark('')">搜索</button>
        </div>
        <div class="nodata searchdata" v-show="isShowSearch">
            <img src="../assets/img/nodata.png" alt="暂无数据">
            <p>暂无搜索，赶紧搜索吧！</p>
        </div>
        <div v-show="isShowResult">
            <div class="nodata searchdata" v-show="list.length==0">
                <img src="../assets/img/nodata.png" alt="暂无数据">
                <p>暂无搜索结果！</p>
            </div>
            <div class="list" v-show="list.length!=0">
                <ul>
                    <li v-for="(item,index) in list" :key="index">
                        <div class="list_left">
                            <router-link :to="{ path:'/info', query: { id: item._id} }">
                                <h3>{{ item.title }}</h3>
                                <p>{{ item.content }}</p>    
                            </router-link>
                            <time>日期：{{ item.create_time }}</time>
                            <span>{{ item.name.name }}</span>
                        </div>
                    </li>
                </ul>
                <div>
                    <p>总共：<strong>{{ count }}</strong> 条记录。</p>
                </div>
                <div class="pageation">
                    <ul>
                        <li v-for="item in allpage" :key="item"><a :class="{active: page==item}" @click="searchMark(item)">{{ item }}</a></li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
// 搜索
import axios from 'axios'
import cookie from '../assets/js/cookie'
import loading from '@/components/common/loading.vue'
export default {
    data () {
        return {
            mgs: 'hello,world!',
            list:[],
            allpage: '',
            page: '',
            count:'',
            key:'',
            isShowSearch: true,
            isLoading: false,
            isShowResult: false,
            token:'',
            user:''
        }
    },
    components: {
        'v-loading': loading
    },
    mounted() {
        var token = cookie.getCookie('token');
        if(token) {
            this.token = token;
        }
        var user = cookie.getCookie('username');
        if(user) {
            this.user = user;
        }
    },
    methods: {
        searchMark (page) {
            if(this.key == '') {
                alert('请输入搜索内容！');
                return false;
            }
            this.isLoading = true;
            this.isShowSearch = false;
            this.isShowResult = false;
            if(page) {
                var current = page;
            }else{
                current = 1;
            }
            this.page = current;
            var url = 'http://localhost:3000/api/search';
            var token = this.token;
            var addr = url + '?token=' + token + '&&key=' + this.key + '&&page=' + current + '&&author=' + this.user;
            axios.post(addr)
             .then((res) => {
                 if(res.data.msg == 'get_succ') {
                    var list = res.data.data.list;
                    list.forEach(element => {
                         if(element.create_time) {
                             element.create_time = (element.create_time).slice(0,20);
                             element.create_time = (element.create_time).split('T');
                             element.create_time = (element.create_time)[0] + ',' + (element.create_time)[1];
                         }
                    });
                    this.list = list;
                    this.allpage = res.data.data.allpage;
                    this.count = res.data.data.count;
                    setTimeout(() => {
                        this.isLoading = false;
                        this.isShowSearch = false;
                        this.isShowResult = true;
                    },1000);
                 }
             })
             .catch((err) => {
                 throw new Error(err);
             })
        }
    }
}
</script>



