<template>
    <div class="user">
        <p class="userinfo">
            亲爱的<strong class="username"> {{user}}</strong>,欢迎光临！
        </p>
        <p class="userinfo">
            <router-link to="/mark/add">
                <span class="add_txt">添加我的便签</span><span class="add_fh">&gt;</span>
            </router-link>
        </p>
        <button class="logout" @click="logout">退出登录</button>
    </div>
</template>
<script>
import axios from 'axios'
import cookie from '../../assets/js/cookie'
export default {
    data () {
        return {
            user: '',
            token: ''
        }
    },
    mounted() {
        var user = cookie.getCookie('username');
        var token = cookie.getCookie('token');
        if(user) {
            this.user = user;
        }
        if(token) {
            this.token = token;
        }
    },
    methods: {
        logout () {
            var res = confirm('你确定要退出登录?');
            if(res) {
                var url = 'http://localhost:3000/user/logout';
                var token = this.token;
                var addr = url + '?token=' + token;
                axios.post(addr)
                .then((res) => {
                    if(res.data.msg == 'save_succ') {
                        alert(res.data.data.info);
                        cookie.setCookie('username',null,-1);
                        cookie.setCookie('token',null,-1);
                        this.$router.push({path: '/login'});
                    }else{
                        alert(res.data.data.info);
                    }
                })
                .catch((err) => {
                    throw new Error(err);
                })
            }
        }
    }
}
</script>
<style lang="scss" scoped>
    .user {
        margin: 30px 0;
        .userinfo {
            padding-left: 30px;
            width: 100%;
            height: 50px;
            line-height: 50px;
            font-size: 16px;
            background-color: #f4f4f4;
            text-align: left;
            .username {
                margin: 0 10px;
                color: #f00;
                font-size: 18px;
            }
            a {
                display: block;
                width: 100%;
                height: 100%;
                text-decoration: none;
                color: #333;
                .add_txt {
                    float: left;
                }
                .add_fh {
                    float: right;
                    margin-right: 60px;
                    font-size: 20px;
                }
            }
        }
        .logout {
            width: 90%;
            height: 50px;
            line-height: 50px;
            font-size: 18px;
            color: #fff;
            outline: none;
            border: none;
            background-color: #dc5151;
            cursor: pointer;
        }
    }
    
</style>