import axios from 'axios'
export default {
    data () {
        return {
            msg: '',
            isShow: false,
            msgpwd: '',
            isShowPwd: false,
            msgcpwd: '',
            isShowCpwd: false,
            username: '',
            password: '',
            cpassword: ''
        }
    },
    methods: {
        loginUser () {
            var nameReg = /^[a-zA-Z0-9_-]{5,16}$/;
            var pwdReg = /^(\w){6,20}$/;
            if(this.username == '') {
                this.isShow = true;
                this.msg = '请输入用户名！';
                return;
            }else if(!nameReg.test(this.username)){
                this.isShow = true;
                this.msg = '请输入6到16位（字母，数字，下划线，减号）！';
                return;
            }else{
                this.isShow = false;
            }
            if(this.password == '') {
                this.isShowPwd = true;
                this.msgpwd = '请输入密码！';
                return;
            }else if(!pwdReg.test(this.password)){
                this.isShowPwd = true;
                this.msgpwd = '请输入6-20个字母、数字、下划线 ！';
                return;
            }else{
                this.isShowPwd = false;
            }
            if(this.cpassword == '') {
                this.isShowCpwd = true;
                this.msgcpwd = '请输入确认密码！';
                return;
            }else if(this.cpassword != this.password){
                this.isShowCpwd = true;
                this.msgcpwd = '两次输入不一样！';
                return;
            }else{
                this.isShowCpwd = false;
            }
            var data = {
                username: this.username,
                password: this.password
            }
            var url = 'http://localhost:3000/user/reg';
            var addr = url + '?username=' + data.username + '&&password=' + data.password;
            axios.post(addr)
                .then((res) => {
                    if(res.data.msg == 'save_succ') {
                        alert(res.data.data.info);
                        this.$router.push('/login');
                    }else{
                        alert(res.data.data.info);
                    }
                })
                .catch((err) => {
                    throw new Error(err);
                })
        }
    }
}