<template>
    <div>
        <div v-show="isLoading">
            <loading></loading>
        </div>
        <div v-show="!isLoading">
            <h2>{{ info.title }}</h2>
            <p><span>作者:<strong>{{ info.author }}</strong></span>,<span>类型:<strong>{{ info.name }}</strong></span>,<span>日期:<strong>{{ info.create_time }}</strong></span></p>
            <br>
            <div class="page">
                <p>{{ info.content }}</p>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
import cookie from '../assets/js/cookie'
import loading from '@/components/common/loading.vue'
export default {
    data () {
        return {
            info: {},
            isLoading: true,
            token:''
        }
    },
    components: {
        loading
    },
    mounted() {
        var token = cookie.getCookie('token');
        if(token) {
            this.token = token;
        }
        var id = this.$route.query.id;
        this.getMarkInfo(id);
    },
    methods: {
        getMarkInfo (id) {
            var url = 'http://localhost:3000/api/mark/info';
            var token = this.token;
            var addr = url + '?token=' + token + '&&id=' + id;
            axios.post(addr)
             .then((res) => {
                 if(res.data.msg == 'get_succ') {
                    var list = res.data.data.list;
                    list.create_time = (list.create_time).slice(0,20);
                    list.create_time = (list.create_time).split('T');
                    list.create_time = (list.create_time)[0] + ',' + (list.create_time)[1];
                    list.name = list.name.name;
                    this.info = list;
                    setTimeout(() => {
                        this.isLoading = false;
                    },1000);
                 }
             })
             .catch((err) => {
                 throw new Error(err);
             })
        }
    }
}
</script>
<style lang="scss" scoped>
    .page {
        margin: 20px auto;
        width: 85%;
        text-align: left;
        p {
            text-indent: 30px;
        }
    }
</style>
