// main
import axios from 'axios'
import cookie from '../../assets/js/cookie'
import loading from '@/components/common/loading'
export default {
    data () {
        return {
            mgs: 'hello,world!',
            list:[],
            allpage: '',
            page: '',
            count:'',
            isLoading: true,
            token:'',
            user:''
        }
    },
    components: {
        loading: loading
    },
    mounted() {
        var token = cookie.getCookie('token');
        if(token) {
            this.token = token;
        }
        var user = cookie.getCookie('username');
        if(user) {
            this.user = user;
        }
        this.getMarkList();
    },
    methods: {
        getMarkList (page) {
            this.isLoading = true;
            var current = 0;
            if(page == undefined) {
                current = 1;
            }else{
                current = page;
            }
            this.page = current;
            var url = 'http://localhost:3000/api/mark/list';
            var token = this.token;
            var addr = url + '?token=' + token + '&&page=' + current + '&&author=' + this.user;
            var header = {
                "Content-Type": "application/x-www-form-urlencoded"
            }
            axios.get(addr,header)
             .then((res) => {
                 if(res.data.msg == 'get_succ') {
                    var list = res.data.data.list;
                    list.forEach(element => {
                         if(element.create_time) {
                             element.create_time = (element.create_time).slice(0,20);
                             element.create_time = (element.create_time).split('T');
                             element.create_time = (element.create_time)[0] + ',' + (element.create_time)[1];
                         }
                    });
                    this.list = list;
                    this.allpage = res.data.data.allpage;
                    this.count = res.data.data.count;
                    setTimeout(() => {
                        this.isLoading = false;
                    },1000);
                 }
             })
             .catch((err) => {
                 throw new Error(err);
             })
        },
        delMark (id) {
            if(id) {
                var res = confirm('你确定要删除吗？');
                if(res) {
                    var url = 'http://localhost:3000/api/mark/del';
                    var token = this.token;
                    var addr = url + '?token=' + token + '&&id=' + id;
                    axios.post(addr)
                    .then((res) => {
                        if(res.data.msg == 'get_succ') {
                            alert('删除成功！');
                            this.getMarkList();
                        }
                    })
                    .catch((err) => {
                        throw new Error(err);
                    })

                }
            }
        }
    }
}