<template>
    <div class="mark">
        <loading v-show="!isLoading"></loading>
        <div v-show="isLoading">
            <h3 v-show="type == 'add'">添加便签</h3>
            <h3 v-show="type == 'edit'">编辑便签</h3>
            <form @submit.prevent="saveInfo" method="post">
                <p>
                    <label for="title">标题：</label>
                    <input type="text" name="title" placeholder="请输入标题" v-model="info.title">
                </p>
                <p v-show="type=='add'">
                    <label for="type">类型：</label>
                    <select name="type" id="type" v-model="info.type">
                        <option value="">请选择</option>
                        <option v-for="item in types" :value="item._id" :key="item._id">{{item.name}}</option>
                    </select>
                </p>
                <input type="hidden" name="author" v-model="info.author">
                <p>
                    <label for="content" class="content">内容：</label>
                    <textarea name="content" id="content" cols="30" rows="10" v-model="info.content"></textarea>
                </p>
                <p>
                    <input type="submit" value="提交" class="save">
                </p>
            </form>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
import cookie from '../assets/js/cookie'
import loading from '@/components/common/loading.vue'
export default {
    data () {
        return {
            info: {
                title: '',
                type:'',
                content: '',
                author: ''
            },
            type:'',
            id: '',
            name_id:'',
            types: [],
            isLoading: true,
            token:''
        }
    },
    components: {
        loading
    },
    watch: {
        $route () {
            this.type = this.$route.params.type;
            this.id = this.$route.query.id;    
        }
    },
    mounted() {
        var token = cookie.getCookie('token');
        if(token) {
            this.token = token;
        }
        var user = cookie.getCookie('username');
        if(user) {
            this.info.author = user;
        }
        this.type = this.$route.params.type;
        this.id = this.$route.query.id;
        if(this.type == 'edit') {
            this.isLoading = false;
            this.getMarkInfo(this.id);
        }else{
            this.isLoading = true;
        }
        this.getType();
       
    },
    methods: {
        getMarkInfo (id) {
            var url = 'http://localhost:3000/api/mark/info';
            var token = this.token;
            var addr = url + '?token=' + token + '&&id=' + id;
            axios.post(addr)
             .then((res) => {
                 if(res.data.msg == 'get_succ') {
                    var list = res.data.data.list;
                    this.info.title = list.title;
                    this.info.content = list.content;
                    this.info.author = list.author;
                    this.info.type = list.name.name;
                    this.name_id = list.name._id;
                    setTimeout(() => {
                       this.isLoading = true; 
                    },1000)
                 }
             })
             .catch((err) => {
                 throw new Error(err);
             })
        },
        getType () {
            var url = 'http://localhost:3000/api/type';
            var token = this.token;
            var addr = url + '?token=' + token;
            axios.post(addr)
             .then((res) => {
                 if(res.data.msg == 'get_succ') {
                    var list = res.data.data.list;
                    this.types = list;
                 }
             })
             .catch((err) => {
                 throw new Error(err);
             })
        },
        saveInfo () {
            if(this.info.title == '') {
                alert('请输入标题！');
                return false;
            }
            if(this.info.type == '') {
                alert('请输入类型！');
                return false;
            }
            if(this.info.content == '') {
                alert('请输入内容！');
                return false;
            }
            var url,token,addr;
            if(this.type == 'add') {
                url = 'http://localhost:3000/api/mark/add';
                token = this.token;
                addr = url + '?token=' + token + '&&title=' + this.info.title + '&&author=' + this.info.author + '&&type=' + this.info.type + '&&content=' + this.info.content;
            }
            if(this.type == 'edit') {
                this.info.type = this.name_id;
                url = 'http://localhost:3000/api/mark/edit';
                token = this.token;
                addr = url + '?token=' + token + '&&title=' + this.info.title + '&&id=' + this.id + '&&author=' + this.info.author + '&&type=' + this.info.type + '&&content=' + this.info.content;
            }
            axios.post(addr)
             .then((res) => {
                 if(res.data.msg == 'find_succ') {
                    alert(res.data.data.info);
                    location.href = '/#/'
                 }else{
                    alert(res.data.data.info);
                 }
             })
             .catch((err) => {
                 throw new Error(err);
             })
        }
    }
}
</script>
<style lang="scss" scoped>
    .mark {
        width: 100%;
        height: 400px;
        label {
            position: relative;
            left: 18px;
            font-size: 13px;
        }
        input,textarea,select {
            position: relative;
            left: 20px;
            width: 202px;
            border: none;
            border-bottom: 1px solid #eee;
            outline: none;
        }
        .content {
            position: relative;
            top: -135px;
            left: 20px;
        }
        textarea {
            position: relative;
            left: 20px;
            border: 1px solid #eee;
        }
        .save {
            position: relative;
            left: 40px;
            width: 212px;
            height: 30px;
            color: #fff;
            background-color: #8287f0;
        }
    }
</style>
